import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";

import CryptoMLMIndex from "../screens/crypto-mlm-software-development";

const CryptoMLMPage = () => {
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Crypto MLM Software Development Company", link: "" },
  ];
  return (
    <Layout>
      <BreadCrumb items={breadcrumbLinks} />
      <CryptoMLMIndex />
    </Layout>
  );
};
export const Head = () => (
  <Seo title="Crypto MLM Software Development Company"></Seo>
);

export default CryptoMLMPage;
